@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro:400,900|Source+Sans+Pro:300,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Courier+Prime:wght@700&display=swap);
header {
    display: flex;
    justify-content: space-between;
    padding: 1em;
}

.logo {
    max-width: 100px;
}

.nav {
    position: fixed;
    background: var(--clr-dark);
    color: var(--clr-light);
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 100;

    transform: translateX(100%);
    transition: transform 250ms cubic-bezier(.5, 0, .5, 1);
}

.nav__list {
    list-style: none;
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin: 0;
    padding: 0;
}

.nav__link {
    color: inherit;
    font-weight: var(--fw-bold);
    font-size: var(--fs-h2);
    text-decoration: none;
}

.nav__link:hover {
    color: var(--clr-accent);
}

.nav-toggle {
    padding: .5em;
    background: transparent;
    border: 0;
    cursor: pointer;
    position: absolute;
    right: 1em;
    top: 1em;
    z-index: 1000;
}

.nav-open .nav {
    transform: translateX(0);
}

.nav-open .nav-toggle {
    position: fixed;
}

.nav-open .hamburger {
    transform: rotate(.625turn);
}

.nav-open .hamburger::before {
    transform: rotate(90deg) translateX(-6px);
}

.nav-open .hamburger::after {
    opacity: 0;
}

.hamburger {
    display: block;
    position: relative;
}

.hamburger,
.hamburger::before,
.hamburger::after {
    background: var(--clr-accent);
    width: 2em;
    height: 3px;
    border-radius: 1em;
    transition: transform 250ms ease-in-out;
}


.hamburger::before,
.hamburger::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
}

.hamburger::before { top: 6px; }
.hamburger::after { bottom: 6px; }

.intro {
    position: relative;
}

.intro__img {
    box-shadow: var(--bs);
}

.section__title__name {
  display: block;
}

.section__subtitle--intro {
    display: inline-block;
}

@media (min-width: 600px) {
    .intro {
        display: grid;
        width: -webkit-min-content;
        width: -moz-min-content;
        width: min-content;
        margin: 0 auto;
        grid-column-gap: 1em;
        grid-template-areas:
            "img title"
            "img subtitle";
        grid-template-columns: -webkit-min-content -webkit-max-content;
        grid-template-columns: min-content max-content;
    }

    .intro__img {
        grid-area: img;
        min-width: 250px;
        position: relative;
        z-index: 2;
    }

    .section__subtitle--intro {
        align-self: start;
        grid-column: -1 / 1;
        grid-row: 2;
        text-align: right;
        position: relative;
        left: -1.5em;
        width: calc(100% + 1.5em);
    }
}

.my-services {
    background-color: var(--clr-dark);
    background-image: url(/dev-portfolio/static/media/services-bg.f1ca14a0.jpg);
    background-size: cover;
    color: var(--clr-light);
    text-align: center;
}

.section__title--services {
    color: var(--clr-accent);
    position: relative;
}

.section__title--services::after {
    content: '';
    display: block;
    width: 2em;
    height: 1px;
    margin: 0.5em auto 1em;
    background: var(--clr-light);
    opacity: 0.25;
}

.services {
    margin-bottom: 4em;
}

.service {
    max-width: 500px;
    margin: 0 auto;
}

@media (min-width: 800px) {
    .services {
        display: flex;
        max-width: 1000px;
        margin-left: auto;
        margin-right: auto;
    }

    .service + .service {
        margin-left: 2em;
    }
}

.about-me {
    max-width: 1000px;
    margin: 0 auto;
}

.about-me__img {
    box-shadow: var(--bs);
}

@media (min-width: 600px) {
    .about-me {
        display: grid;
        grid-template-columns: 1fr 200px;
        grid-template-areas:
            "title img"
            "subtitle img"
            "text img";
        grid-column-gap: 2em;
    }

    .section__title--about {
        grid-area: title;
    }

    .section__subtitle--about {
        grid-column: 1 / -1;
        grid-row: 2;
        position: relative;
        left: -1em;
        width: calc(100% + 2em);
        padding-left: 1em;
        padding-right: calc(200px + 4em);
    }

    .about-me__img {
        grid-area: img;
        position: relative;
        z-index: 2;
    }
}

.project-intro {
    position: relative;
}

.project-intro__title {
    display: block;
    margin-top: .25em;
    margin-bottom: .25em;
    font-weight: var(--fw-reg);
    text-align: center;
}

.project-intro__subtitle {
    display: inline-block;
    margin: 0;
    font-size: var(--fs-h3);
    background: var(--clr-accent);
    font-family: var(--ff-secondary);
    margin-bottom: 1em;
}

.project-intro__subtitle--overflow {
    display: inline-block;
    margin: 0;
    font-size: var(--fs-h3);
    background: var(--clr-accent);
    font-family: var(--ff-secondary);
    visibility: hidden;
}

.project-intro__img {
    box-shadow: var(--bs);
}

@media (min-width: 600px) {
    .project-intro {
        display: grid;
        width: -webkit-min-content;
        width: -moz-min-content;
        width: min-content;
        margin: 0 auto;
        grid-column-gap: 1em;
        grid-template-areas:
            "img title"
            "img subtitle";
        grid-template-columns: -webkit-min-content -webkit-max-content;
        grid-template-columns: min-content max-content;
    }

    .project-intro__img {
        grid-area: img;
        min-width: 250px;
        position: relative;
        z-index: 2;
    }

    .project-intro__title {
        text-align: center;
    }

    .project-intro__subtitle {
        align-self: start;
        grid-column-start: -1;
        grid-column-end: -2;
        grid-row: 2;
        text-align: right;
        position: relative;
        right: 0;
        width: calc(100%);
        margin-bottom: 0;
    }

    .project-intro__subtitle--overflow {
        align-self: start;
        grid-column-start: -1;
        grid-column-end: -3 ;
        grid-row: 2;
        text-align: right;
        position: relative;
        left: -1.5em;
        width: calc(100% + 1.5em);
        visibility: visible;
    }
}

.project-social-list {
    list-style: none;
    display: flex;
    justify-content: center;
    padding-left: 0;
    color: var(--clr-accent);
    text-align: center;
    font-size: var(--fs-h3);
}

.project-social-list__item {
    margin: 0 .5em;
    display: flex;
    flex-direction: column;
}

.project-social-list__link {
    padding-left: .5em;
    padding-right: .5em;
    transition: transform 200ms ease-in-out;
}

.project-social-list__link:hover {
    transform: scale(1.3);
}

.project-social-list__linkText {
  color: var(--clr-light);
  font-size: var(--fs-body);
}

.project-social-list a {
    color: inherit;
    text-decoration: none;
}

/* For Phones and Tablets */
.portfolio__item {
  background: var(--clr-accent);
  overflow: hidden;
  position: relative;
}

.portfolio__keyword {
  position: absolute;
  bottom: 2em;
  right: 1em;
  opacity: 1;
  color: var(--clr-light);
  font-family: var(--ff-code);
}

/* For desktop */
@media only screen and (min-width: 1024px) {
  .portfolio__img {
    transition:
      transform 750ms cubic-bezier(.5, 0, .5, 1),
      opacity 250ms linear;
  }

  .portfolio__item:focus {
    z-index: 2;
  }

  .portfolio__img:hover,
  .portfolio__item:focus .portfolio__img {
    transform: scale(1.2);
    opacity: .5;
  }

  .portfolio__keyword {
    right: -5em;
    opacity: 0;
    transition:
      right 150ms linear,
      opacity 500ms linear;
  }

  .portfolio__item:hover .portfolio__keyword {
    opacity: 1;
    right: 1em;
  }

  .portfolio__item:focus .portfolio__keyword {
    opacity: 1;
    right: 1em;
  }
}

.portfolio {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.my-work {
    background-color: var(--clr-dark);
    color: var(--clr-light);
    text-align: center;
}

.footer {
    background: #111;
    color: var(--clr-accent);
    text-align: center;
    padding: 2.5em 0;
    font-size: var(--fs-h3);
}

.footer a {
    color: inherit;
    text-decoration: none;
}

.footer__link {
    font-weight: var(--fw-bold);
}

.footer__link:hover,
.social-list__link:hover {
    opacity: .7;
}

.footer__link:hover {
    text-decoration: underline;
}

.social-list {
    list-style: none;
    display: flex;
    justify-content: center;
    margin: 2em 0 0;
    padding: 0;
}

.social-list__item {
    margin: 0 .5em;
}

.social-list__link {
    padding: .5em;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

/* Custom Properties, update these for your own design */

:root {
    --ff-primary: 'Source Sans Pro', sans-serif;
    --ff-secondary: 'Source Code Pro', monospace;
    --ff-code: 'Courier Prime', monospace;

    --fw-reg: 300;
    --fw-bold: 900;

    --clr-light: #fff;
    --clr-dark: #303030;
    --clr-accent: #16e0bd;

    --fs-h1: 3rem;
    --fs-h2: 2.25rem;
    --fs-h3: 1.25rem;
    --fs-body: 1rem;

    --bs: 0.25em 0.25em 0.75em rgba(0,0,0,.25),
          0.125em 0.125em 0.25em rgba(0,0,0,.15);
}

@media (min-width: 800px) {
    :root {
        --fs-h1: 4.5rem;
        --fs-h2: 3.75rem;
        --fs-h3: 1.5rem;
        --fs-body: 1.125rem;
    }
}

/* General styles */

html {
    scroll-behavior: smooth;
}

body {
    background: #fff;
    background: var(--clr-light);
    color: #303030;
    color: var(--clr-dark);
    margin: 0;
    font-family: 'Source Sans Pro', sans-serif;
    font-family: var(--ff-primary);
    font-size: 1rem;
    font-size: var(--fs-body);
    line-height: 1.6;
}

section {
    padding: 5em 2em;
}

img {
    display: block;
    max-width: 100%;
}

strong { font-weight: 900; font-weight: var(--fw-bold) }

:focus {
    outline: 3px solid #16e0bd;
    outline: 3px solid var(--clr-accent);
    outline-offset: 3px;
}

/* Buttons */

.btn {
    display: inline-block;
    padding: .5em 2.5em;
    background: #16e0bd;
    background: var(--clr-accent);
    color: #303030;
    color: var(--clr-dark);
    text-decoration: none;
    cursor: pointer;
    font-size: .8rem;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 900;
    font-weight: var(--fw-bold);
    transition: transform 200ms ease-in-out;
}

.btn:hover {
    transform: scale(1.1);
}

/* Typography */

h1,
h2,
h3 {
    line-height: 1.1;
    margin: 0;
}

h1 { font-size: 3rem; font-size: var(--fs-h1) }
h2 { font-size: 2.25rem; font-size: var(--fs-h2) }
h3 { font-size: 1.25rem; font-size: var(--fs-h3) }


.section__title {
    margin-bottom: .25em;
}

.section__title--intro {
    font-weight: 300;
    font-weight: var(--fw-reg);
}

.section__subtitle {
    margin: 0;
    font-size: 1.25rem;
    font-size: var(--fs-h3);
}

.section__subtitle--intro,
.section__subtitle--about {
    background: #16e0bd;
    background: var(--clr-accent);
    font-family: 'Source Code Pro', monospace;
    font-family: var(--ff-secondary);
}

.section__subtitle--work {
    color: #16e0bd;
    color: var(--clr-accent);
    font-weight: 900;
    font-weight: var(--fw-bold);
    margin-bottom: 2em;
}

